___CSS_LOADER_EXPORT___.push([module.id, `/* Container for all services */
.our-services-section {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  padding: 40px 20px;
  background-color: #f0f0f0;
}

/* Title Row */
.hero-content-title {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
}

/* Slogan */
.our-services-slogan {
  width: 100%;
  text-align: center;
  font-size: 34px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: bolder;
  color: rgb(35, 53, 51);
  line-height: 40px;
  margin: 15px auto 40px auto;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

/* Service Card */
.service-card {
  background-color: #fff;
  border-radius: 8px;
  width: 445px;
  margin: 0 10px 20px 10px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  overflow: hidden;
  position: relative;
  transition: transform 0.3s ease-in-out;
}

/* FRONT & BACK are normal block elements */
.service-front,
.service-back {
  padding: 20px;
  text-align: center;
}

/* FRONT: white background with circle image */
.service-front {
  background-color: #fff;
}

/* BACK: on desktop, teal background; on mobile, white background */
.service-back {
  background-color: #628a88;
  color: #fff;
  display: none;
}

/* Circle image on the front */
.service-image {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 10px;
}

/* Circle image on the back (only visible on mobile) */
.service-image-back {
  display: none;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 10px;
}

/* Title on the front */
.service-title {
  font-size: 1.2rem;
  font-weight: bold;
  color: #333;
  margin: 0 0 5px 0;
}

/* Description on the back */
.service-description {
  font-size: 1rem;
  line-height: 1.4;
  margin-bottom: 15px;
}

/* Link text on both sides */
.service-link {
  font-size: 0.9rem;
  text-decoration: underline;
  font-weight: bold;
  color: inherit;
}

/* -------------------------------------
   Mobile (≤768px):
   - Hide front, show back
   - Use white background for back and dark text
   - Show circle image on back
-------------------------------------- */
@media (max-width: 768px) {
  .service-front {
    display: none;
  }
  .service-back {
    display: block;
    background-color: #fff;
    color: #333;
  }
  .service-image-back {
    display: block;
    margin: 0 auto 10px auto;
  }
}

/* -------------------------------------
   Desktop (≥769px):
   - Show front by default, swap to back on hover
-------------------------------------- */
@media (min-width: 769px) {
  .service-card:hover {
    transform: translateY(-4px);
  }
  .service-front {
    display: block;
  }
  .service-back {
    display: none;
  }
  .service-card:hover .service-front {
    display: none;
  }
  .service-card:hover .service-back {
    display: block;
  }
}
